// news home
.news-home {
  padding: 0px 0 60px;
  &__title {
    margin-top: 1.4em;
    font-weight: 500;
    font-size: 12px;
    text-transform: uppercase;
    opacity: 0.6; }
  &__grid {
    display: flex;
    flex-wrap: wrap;
    margin: 10px -15px -15px; }
  &__col {
    width: 100%;
    min-width: 1px;
    min-height: 0;
    flex: 0 0 $col4;
    max-width: $col4;
    padding: 15px;
    display: flex;
    flex-direction: column; }
  &__card {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    &:hover {
      .news-home__content {
        color: $colorOrange; } } }

  &__date {
    font-size: 13px;
    line-height: 16px; }

  &__content {
    max-width: 350px;
    margin-top: 0.8em;
    font-weight: 600;
    font-size: 16px;
    line-height: 1.75;
    transition: color .25s $ease; }

  &__cta-link {
    display: flex;
    justify-content: flex-end;
    margin-right: 60px; }

  &__more-link {
    position: relative;
    margin-top: 20px;
    display: flex;
    align-items: center;
    span {
      display: inline-block;
      padding-right: 10px;
      font-weight: 600;
      font-size: 14px;
      line-height: 17px;
      margin-top: 4px;
      text-transform: uppercase; }
    .ul-ico-arrow {
      position: relative;
      vertical-align: middle;
      display: inline-block;
      size: 17px 9px;
      &::before, &::after {
        display: inline-block;
        content: ' ';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        transition: .25s $ease; }
      &::before {
        left: 0px;
        background: white;
        size: 12px 2px;
        border-radius: 3px; }
      &::after {
        right: 1px;
        size: 7px;
        border: 2px solid white;
        transform: translateY(-50%) rotate(45deg);
        border-left: none;
        border-bottom: none;
        transition: .25s $ease; } }
    &:hover {
      .ul-ico-arrow {
        &::after {
          transform: translate(9px, -50%) rotate(45deg);
          border-color: $colorOrange; }
        &::before {
          size: 25px 2px;
          background-color: $colorOrange; } } } } }


@include r($hd) {
  .news-home {
    margin-right: 50px;
    padding-bottom: 40px;
    &__content {
      max-width: 300px;
      font-size: 14px; }
    &__cta-link {
      margin-right: 0px; } } }

@include r($lg) {
  .news-home {
    //slider goes here
    margin-right: 0;
    &__grid {
      margin-right: 0; }
    &__col {
      // flex-basis: 300px
      // max-width: 300px
      flex-basis: auto;
      max-width: 100%; }
    &__cta-link {
      margin-right: 20px; } } }

@include r($sm) {
  .news-home {
    background: white;
    margin-left: -20px;
    padding-left: 20px;
    padding-bottom: 25px;
    color: $fontColor;
    &__grid {
      margin-top: 0; }
    &__col {
      flex-basis: 300px;
      max-width: 300px; }
    &__cta-link {
      justify-content: flex-start; }
    &__more-link {
      .ul-ico-arrow {
        &:before {
          background: #000; }
        &:after {
          border-color: #000; } } }
    &__date {
      font-size: 12px; }
    &__content {
      font-size: 14px; }
    &__card {
      &:hover {
        .news-home__content {
          color: $colorPrimary; } } } } }

@include r(414) {
  .news-home {
    &__col {
      // flex-basis: calc(100% - 30px)
      // max-width: calc(100% - 30px)
      flex-basis: 100%;
      max-width: 100%; }
    &__card {
      padding-right: 15px; } } }
